
.image-box {
    padding-top: 2rem;
    box-shadow: inset 0 0 0 100vw rgba(0, 0, 0, 0.4);
    background: var(--image-url) center center;
    background-position-x: center;
    background-position-y: center;

    background-size: cover;
    color: white;
    min-height: 50vh;
    height: max-content;
    display: relative;
    align-items: center;
    justify-content: center;
    text-align: center;
    transition: box-shadow .3s ease-out;
    padding-bottom:2rem;

}

@media screen and (min-width: 700px) {
    .somepadding {
    padding:1rem;
    }
}

.imageBackground{
    width: 100%;
}
.somepadding{
    padding:1rem;
}

.image-box:hover {
    transition: box-shadow .3s ease-in;

    box-shadow: inset 0 0 0 100vw rgba(0, 0, 0, 0.2);
}
.isvisible {
    transition: box-shadow .3s ease-in;
    box-shadow: inset 0 0 0 100vw rgba(0, 0, 0, 0.2);
}
.shadow {
    text-shadow: 2px 2px rgba(0, 0, 0, 0.6);
    box-shadow: none;
}

.buttons{
    border-radius: 7px;
    font-size:1.1rem;
    padding:10px;
    background: rgb(59,59,59);
    background: linear-gradient(0deg, rgba(59,59,59,1) 0%, rgba(103,103,103,1) 100%);
    color:white;
}
.buttons:hover{
    cursor: pointer;
    background: rgb(59,59,59);
    background: linear-gradient(0deg, rgba(103,103,103,1) 0%, rgba(59,59,59,1) 100%);

    
}